.product {
  padding-top: 11px;
  padding-bottom: 14px;
  margin-bottom: 19px;
  width: 280px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  box-sizing: border-box;
  position: relative;

  @media (max-width: @mobile-width-only) {
    &::before {
      content: "";
      position: absolute;
      top: -8px;
      left: -767px;
      width: 1000%;
      height: 1px;
      background-color: @special-dark-grey;
    }
  }

  @media (min-width: @tablet-width) {
    width: 324px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    background-image: linear-gradient(@white 16%, @basic-grey 16%);
    margin-bottom: 0;
    padding: 0 36px 39px;
    padding-top: 0;
  }

  @media (min-width: @desktop-width) {
    width: 245px;
    padding: 0 30px 40px 35px;
  }

  &:nth-last-of-type(2) {
    margin-bottom: 36px;

    @media (min-width: @tablet-width) {
      margin-bottom: 0;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.product__container {
  @media (min-width: @tablet-width) {
    width: 252px;
  }

  @media (min-width: @desktop-width) {
    width: 169px;
  }
}

.product__link {
  text-decoration: none;

  &--image {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media (min-width: @tablet-width) {
    text-align: center;
  }
}

.product__image {
  display: block;
  .image;

  @media (min-width: @tablet-width) {
    width: 163px;
    height: 174px;
    margin-bottom: 34px;

    &--size {
      width: 168px;
      height: 200px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 46px;
  }
}

.product__title {
  margin: 0;
  font-size: 16px;
  text-transform: uppercase;
  color: @darkest-gray;
  margin-bottom: 20px;
  font-weight: 400;
  width: 76px;

  br {
    display: none;
  }

  @media (min-width: @tablet-width) {
    width: auto;
    font-size: 20px;
    line-height: 24px;

    br {
      display: block;
    }
  }

  @media (min-width: @desktop-width) {
    line-height: 21px;
  }
}

.product__description-list {
  margin: 0;
  margin-bottom: 22px;
  padding: 0;
  font-family: @addition-font-family;
  font-size: 12px;
  line-height: 16px;
  color: @dark-grey;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 42px;
  }
}

.product__description-list-wrapper {
  display: flex;
  justify-content: space-between;
  position: relative;

  @media (min-width: @tablet-width) {
    margin-bottom: 14px;

    &:last-child {
      margin-bottom: 33px;
    }

    &::after {
      content: "";
      height: 1px;
      position: absolute;
      top: 25px;
      right: 0;
      left: 0;
      background-color: @special-gray;
    }
  }
}

.product__description-list-data {
  margin: 0;
}

.product__button {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 40px;
  grid-column: 1/3;

  &--more {
    color: @dark-grey;
    background-color: @basic-grey;
  }

  @media (min-width: @tablet-width) {
    width: 248px;
  }

  @media (min-width: @desktop-width) {
    width: 168px;
  }
}

.product__button--more:hover {
  background-color: @special-dark-grey;
}

.product__button--more:active {
  background-color: @special-dark-grey;
  color: rgba(68, 68, 68, 0.3);
}

.product__text-show {
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: @light-black;
  margin: 0;
  margin-bottom: 8px;

  br {
    display: none;
  }

  @media (min-width: @tablet-width) {
    text-align: center;
    font-size: 20px;
    line-height: 22px;
    margin-bottom: 23px;

    br {
      display: block;
    }
  }
}

.product__text {
  font-family: @addition-font-family;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: @dark-grey;
  margin: 0;
  margin-bottom: 21px;

  br {
    display: none;
  }

  @media (min-width: @tablet-width) {
    text-align: center;
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 87px;

    br {
      display: block;
    }
  }
}
